@charset "UTF-8";
.ui-btn-s,
.ui-btn,
.ui-btn-lg,
.ui-btn-lg-nowhole {
  position: relative;
  text-align: center;
  background-color: #fff;
  vertical-align: top;
  color: black;
  -webkit-box-sizing: border-box;
  background-clip: padding-box;
  border: 1px solid #c3c8cc;
  border: 1px solid #c3c8cc;
  border-radius: 3px; }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .ui-btn-s,
    .ui-btn,
    .ui-btn-lg,
    .ui-btn-lg-nowhole {
      position: relative;
      border: 0; }
      .ui-btn-s:before,
      .ui-btn:before,
      .ui-btn-lg:before,
      .ui-btn-lg-nowhole:before {
        content: "";
        width: 200%;
        height: 200%;
        position: absolute;
        top: 0;
        left: 0;
        border: 1px solid #c3c8cc;
        -webkit-transform: scale(0.5);
        -webkit-transform-origin: 0 0;
        padding: 1px;
        -webkit-box-sizing: border-box;
        border-radius: 6px;
        pointer-events: none; } }
  @media screen and (-webkit-min-device-pixel-ratio: 3) {
    .ui-btn-s,
    .ui-btn,
    .ui-btn-lg,
    .ui-btn-lg-nowhole {
      position: relative;
      border: 0; }
      .ui-btn-s:before,
      .ui-btn:before,
      .ui-btn-lg:before,
      .ui-btn-lg-nowhole:before {
        content: "";
        width: 300%;
        height: 300%;
        position: absolute;
        top: 0;
        left: 0;
        border: 1px solid #c3c8cc;
        -webkit-transform: scale(0.3333);
        -webkit-transform-origin: 0 0;
        padding: 1px;
        -webkit-box-sizing: border-box;
        border-radius: 6px;
        pointer-events: none; } }
  .ui-btn-s:focus,
  .ui-btn:focus,
  .ui-btn-lg:focus,
  .ui-btn-lg-nowhole:focus {
    outline: none; }
  .ui-btn-s:not(.disabled):not(:disabled):active, .ui-btn-s.active,
  .ui-btn:not(.disabled):not(:disabled):active,
  .ui-btn.active,
  .ui-btn-lg:not(.disabled):not(:disabled):active,
  .ui-btn-lg.active,
  .ui-btn-lg-nowhole:not(.disabled):not(:disabled):active,
  .ui-btn-lg-nowhole.active {
    box-shadow: 0 0 0 1000px rgba(0, 0, 0, 0.1) inset; }
  .ui-btn-s:after,
  .ui-btn:after,
  .ui-btn-lg:after,
  .ui-btn-lg-nowhole:after {
    content: "";
    position: absolute;
    top: -7px;
    bottom: -7px;
    left: 0;
    right: 0; }
  .ui-btn-s.disabled, .ui-btn-s:disabled,
  .ui-btn.disabled,
  .ui-btn:disabled,
  .ui-btn-lg.disabled,
  .ui-btn-lg:disabled,
  .ui-btn-lg-nowhole.disabled,
  .ui-btn-lg-nowhole:disabled {
    border: 0;
    color: #BBBBBB;
    background: #e9ebec;
    background-clip: padding-box; }

.ui-btn {
  height: 30px;
  line-height: 30px;
  padding: 0px 16px;
  display: block;
  text-align: center;
  font-size: 14px;
  border-radius: 2px;
  box-sizing: border-box;
  overflow: hidden; }

.ui-btn-s {
  width: 60px;
  height: 30px;
  line-height: 30px;
  display: block;
  text-align: center;
  font-size: 14px;
  border-radius: 2px;
  padding: 0px;
  overflow: hidden;
  white-space: nowrap; }

.ui-btn-highlight {
  background-color: transparent;
  color: #fff; }
  .ui-btn-highlight:not(.disabled):not(:disabled):active, .ui-btn-highlight.active {
    box-shadow: 0 0 0 1000px rgba(0, 0, 0, 0.1) inset; }
  .ui-btn-highlight.disabled, .ui-btn-highlight:disabled {
    /* 按钮: */
    font-size: 14px;
    color: #a9a9a9;
    background-color: #8d8d8d;
    letter-spacing: 0px;
    line-height: 30px;
    border: 0; }

.ui-btn-lg {
  font-size: 0.3rem;
  height: 0.8rem;
  line-height: 0.8rem;
  display: block;
  text-align: center;
  width: 100%;
  border-radius: 0; }

.ui-btn-primary {
  border: 0;
  background-color: #5E7358;
  color: white;
  background-clip: padding-box; }
  .ui-btn-primary:not(.disabled):not(:disabled):active, .ui-btn-primary.active {
    box-shadow: 0 0 0 1000px rgba(0, 0, 0, 0.1) inset; }

.ui-btn-danger {
  border: 0;
  background-color: #FB6155;
  color: white;
  background-clip: padding-box; }
  .ui-btn-danger:not(.disabled):not(:disabled):active, .ui-btn-danger.active {
    box-shadow: 0 0 0 1000px rgba(0, 0, 0, 0.1) inset; }

.ui-btn-highlight {
  background-color: transparent; }

.ui-btn-lg-nowhole {
  display: block;
  text-align: center;
  width: 220px;
  height: 40px;
  line-height: 40px;
  font-size: 0.3rem;
  border-radius: 0;
  margin: auto; }

.ui-btn-wrap {
  padding: 10px 12px;
  display: -webkit-box;
  -webkit-box-pack: center; }

@media screen and (-webkit-min-device-pixel-ratio: 2) {
  .ui-btn,
  .ui-btn-lg,
  .ui-btn-s,
  .ui-btn-lg-nowhole {
    border: 0; }
    .ui-btn.disabled:before, .ui-btn:disabled:before,
    .ui-btn-lg.disabled:before,
    .ui-btn-lg:disabled:before,
    .ui-btn-s.disabled:before,
    .ui-btn-s:disabled:before,
    .ui-btn-lg-nowhole.disabled:before,
    .ui-btn-lg-nowhole:disabled:before {
      content: none; }
  .ui-btn-primary:before,
  .ui-btn-danger:before {
    content: none; } }
